* {
  margin: 0px;
  padding: 0px;
}
.app {
  text-align: center;
  font-family: sans-serif;
}
.server-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  gap: 7px;
}
.server-card {
  display: flex;
  flex-direction: column;
  width: min-content;
  padding: 7px;
  border-radius: 7px;
  background-color: #D8D8D8;
  align-items: center;
  gap: 7px;
}
.server-card-icon {
  width: 100px;
}
.subserver-card {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 7px;

  background-color: #C8C8C8;
  border-radius: 7px;
  padding: 7px;
}
.subserver-card:only-child {
  grid-column: span 2;
}
.subserver-card-num {
  background: linear-gradient(140deg, #1400FF 0%, #CE00D2 100%);;
  border-radius: 100px;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #D8D8D8;
}
.subservers-container {
  width: fit-content;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 7px;
  justify-content: center;
}
* {
  white-space: nowrap;
}
.react-tooltip {
  word-wrap: normal;
  white-space: break-spaces;
}